.hide-default-tab-header .v-tabs{display:none}.menu-tab-text{text-transform:none !important}.slide-fade-enter-active{-webkit-transition:all .3s ease;transition:all .3s ease}.slide-fade-leave-active{-webkit-transition:all .8s cubic-bezier(1, 0.5, 0.8, 1);transition:all .8s cubic-bezier(1, 0.5, 0.8, 1)}.slide-fade-enter,.slide-fade-leave-to{-webkit-transform:translateX(10px);transform:translateX(10px);opacity:0}